<template>
        <div id="home">
            <div class="rent-calssroom-form">
                <div class="form-input-container">
                    <div class="input-wrapper">
                        <p class="input-title">Ë™≤Á®ãÂêçÁ®±</p>
                        <input type="text" 
                            v-model="formInputData.classes"
                        >
                    </div>

                    <div class="input-wrapper">
                        <p class="input-title">ÊéàË™≤ÊïôÂ∏´</p>
                        <input type="text" 
                            v-model="formInputData.teacher"
                        >
                    </div>

                    <div class="input-wrapper">
                        <p class="input-title">Áî®ÈÄî</p>
                        <input type="text" 
                            v-model="formInputData.description"
                        >
                    </div>

                    <div class="input-wrapper">
                        <p class="input-title">ÂÄüÁî®Âô®Êùê</p>
                        <input type="text" 
                            v-model="formInputData.toolsRent"
                            placeholder="Ëß∏ÁôºÂô®„ÄÅÊâìÂÖâÁáà„ÄÅ..."
                        >
                    </div>

                    <div class="input-wrapper">
                        <p class="input-title">ÂÄüÁî®‰∫∫ Email</p>
                        <input type="email" 
                            v-model="formInputData.userMail" 
                            id="input_email"
                            placeholder="user@gmail.com" 
                        >
                    </div>
                </div>

                <div class="input-wrapper">
                    <div class="form-input-container">
                            <input class="user-name" type="text"  v-model="formInputData.applyUser.name" placeholder="ÂÄüÁî®‰∫∫">
                            <input class="class-number" type="text" v-model="formInputData.applyUser.classNum" placeholder="Áè≠Á¥öÂ∫ßËôü">
                            <input class="student-id" type="text" v-model="formInputData.applyUser.studentID" placeholder="Â≠∏Ëôü">
                            <input class="phone" type="text" v-model="formInputData.applyUser.phone" placeholder="ÈõªË©±">
                    </div>
                    <div class="form-input-container">
                            <input class="user-name" type="text" v-model="formInputData.agentUser.name" placeholder="‰ª£ÁêÜ‰∫∫">
                            <input class="class-number" type="text" v-model="formInputData.agentUser.classNum" placeholder="Áè≠Á¥öÂ∫ßËôü">
                            <input class="student-id" type="text" v-model="formInputData.agentUser.studentID" placeholder="Â≠∏Ëôü">
                            <input class="phone" type="text" v-model="formInputData.agentUser.phone" placeholder="ÈõªË©±">
                    </div>
                    
                    <hr>
                    <ArrayInput v-model:formInputData="formInputData" />
                </div>
                
                <div class="input-wrapper">
                    <p class="input-title">Ê¨≤ÂÄüÁî®Êó•Êúü</p> 
                    <input type="date"
                        id="input_rentDate"
                        v-model="formInputData.rentDate"
                        @change="checkDate($event)"
                    >
                    <p id="warn-p">Ë®ª: Âè™ËÉΩÂÄüÁî®Êú™‰æÜ3Âë®ÂÖßÁöÑÊôÇÈñì</p>

                    <p class="input-title">ÂÄüÁî®ÊôÇÊÆµ (ÂèØË§áÈÅ∏)</p>
                    <div class="time-period-select" @click="selectPeriod($event)">
                        <div class="time-period" v-for="(period, index) in timePeriod" :key="period" :id="`time-${index}`">{{period}}</div>
                    </div>
                </div>
                
                <div class="btn submit-btn" @click="submit"><b>Êèê‰∫§Áî≥Ë´ã</b></div>
            </div>

        </div>

        <div class="info-btn-wrapper">
            <div class="btn info-btn info" @click="reload"><b>üîÑ ÈáçÊï¥Êó•ÊõÜ</b></div>
        </div>

        <iframe src="https://calendar.google.com/calendar/embed?src=gm.ttu.edu.tw_8o871m4gfckf3o49nlhkv5cpdk%40group.calendar.google.com&ctz=Asia%2FTaipei" 
            id="Calendar"
            style="border: none" width="800" height="600" frameborder="0" scrolling="yes">
        </iframe>


</template>

<script lang="ts">
   import {watch, defineComponent, reactive} from 'vue'
   import Joi from 'joi'
   import { useToast } from "vue-toastification"
   import emailjs from 'emailjs-com'
   import{ init } from 'emailjs-com';
   import date from 'date-and-time';
   import {formValidation} from '../validation/form_validation'
   import ArrayInput from '../components/ArrayInput.vue'


   export default defineComponent({
       name: 'Home',
       components:{ArrayInput},
       setup(){
            
            // emailjs.com
           init("user_WjYj4YxrXX5vvj4wlw4nv");
           const ServiceID = 'cyfan'
           const TemplateID= 'template_t8exz6j'

           const toast = useToast()
           const timePeriod = reactive<Array<string>>([
               '08:10 ~ 11:30', 
               '13:00 ~ 16:30', 
               '17:00 ~ 22:00',
               'ÂÖ®Â§©'
           ])

           const formInputData = reactive({
               classes: '',
               teacher: '',
               description: '',
               toolsRent: '',

               userMail: '',
               applyUser: {
                   name: '',
                   classNum: '',
                   studentID: '',
                   phone: ''
               },

               agentUser:{
                   name: '',
                   classNum: '',
                   studentID: '',
                   phone: ''
               },
               classMate:[],
               classMateString: '',
               applyDate: date.format(new Date(), 'YYYY-MM-DD'),
               rentDate: '',
               TimePeriod: '',
           })

            const selectPeriod = (e: Event): void =>{
                const target: HTMLDivElement = (e.target as HTMLDivElement)
                if(target.className === 'time-period-select') return

                if (target.innerText === 'ÂÖ®Â§©') {
                    Array.from(document.getElementsByClassName('time-period'))
                        .forEach(element => {
                            element.classList.remove('selected')
                        });
                    target.classList.add('selected')
                    formInputData.TimePeriod = 'ÂÖ®Â§©'
                    return
                }

                if(formInputData.TimePeriod === 'ÂÖ®Â§©'){
                    Array.from(document.getElementsByClassName('time-period'))
                        .forEach(element => {
                            element.classList.remove('selected')
                        });
                    formInputData.TimePeriod = ''

                }

                if(target.classList.contains('selected')){
                    target.classList.remove('selected')
                    formInputData.TimePeriod = formInputData.TimePeriod.replace(target.innerHTML + ' | ', '')
                }else{
                    target.classList.add('selected')
                    formInputData.TimePeriod += target.innerHTML + ' | '

                }

                
            }

            const checkDate = (e: Event): void =>{
                const rentDate = new Date((e.target as HTMLInputElement).value)
                const today = new Date(formInputData.applyDate)
                const sub = date.subtract(rentDate, today).toDays()
                const target = (document.getElementById('input_rentDate') as HTMLInputElement)
                if (sub<= 0 || sub > 21){
                    target.classList.add('input-invalid')
                }
                else{
                    target.classList.remove('input-invalid')
                }
            }
           


           const submit = async ()=>{

                // Check if cool down
                const lastSend: string | null  = localStorage.getItem('lastSend')
                if(lastSend){
                    const last = new Date(lastSend)
                    const sub = date.subtract(new Date, last).toMinutes()

                    if (sub < 10){
                        toast.warning(`Áî≥Ë´ãÊèê‰∫§ÂÜ∑Âçª‰∏≠ ! ÈÇÑÈúÄÁ≠âÂæÖ ${10 - Math.round(sub)} ÂàÜÈêò`)
                        return
                    }
                }

                formInputData.classMate.forEach((user: any)=>{
                    formInputData.classMateString += `„ÄêÂßìÂêç: ${user.name}  /  Áè≠Á¥öÂ∫ßËôü: ${user.classNum}  /  Â≠∏Ëôü: ${user.studentID}  /  ÈõªË©±: ${user.phone}„Äë    ||    `
                })

                // Data Validate
               try {
                   const validation = await formValidation.validate(formInputData)
                   if(validation.error){
                       console.log(validation)
                       toast.error(validation.error.toString())
                       return
                   }
               } 
               catch (error) {
                   toast.error(error)
               }

                let inputInvalid = false
               Array.from(document.getElementsByTagName('input'))
                .forEach(element =>{
                    if(element.classList.contains('input-invalid')){
                        inputInvalid = true
                    }
                })

                if (inputInvalid) return
                
                //send email
                // todo: cooldown
                emailjs.send(ServiceID,TemplateID, formInputData)
                    .then(
                        (response): void=>{
                            console.log('SUCCESS!', response.status);
                            toast.success('Â∑≤ÊàêÂäüÈÄÅÂá∫Áî≥Ë´ãÂñÆÔºåË´ãÈùúÂæÖÊâπÂáÜÂõûË¶Ü‰ø°‰ª∂')
                            const now = new Date()
                            localStorage.setItem('lastSend', now.toString())

                        }, 
                        (err): void=>{
                            toast.error(err)
                        }
                    );
           }

           watch(
               ()=>formInputData.userMail, 
               async (mail)=>{
                   const validation = Joi.object({
                        eMail: Joi.string()
                            .email({ tlds: {allow: false} })
                            .min(6)
                            .required()
                    })

                   try {
                       const va = await validation.validate({eMail: mail})
                           const target = (document.getElementById('input_email') as HTMLInputElement)
                       if (va.error) {
                            target.classList.add('input-invalid')
                       }
                       else{
                           target.classList.remove('input-invalid')
                       }
                   } catch (error) {
                       toast.success(error)
                   }
               }
            )


            const reload = (): void=>{
                const target: HTMLIFrameElement = (document.getElementById('Calendar') as HTMLIFrameElement)
                const nsrc = target.src
                target.src = nsrc
            }

          
           return{
               date,
               timePeriod,
               submit,
               selectPeriod,
               formInputData,
               checkDate,
               reload
               
           }
       }
   })
</script>

<style scoped lang="scss">

@mixin btn($color){
    cursor: pointer;
    color: rgb(85, 85, 85);
    padding: 5px;
    border-radius: 5px;
    background-color: $color;
}

#home{
    display: flex;
    justify-content: center;
}

iframe{
    width: 80%;
    margin-bottom: 50px;
    border-radius: 5px;
    margin-top: 10px;
}

.rent-calssroom-form{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    width: 70vh;
    margin: 50px;
    padding: 20px;
    box-shadow: 5px 5px 20px 5px rgb(38, 38, 38);
    border-radius: 10px;

    .input-title{
        text-align: left;
        margin-bottom: 5px;
    }

    .input-note{
        text-align: left;
        margin: 1px;
        color: grey;
        font-size: 12px;
    }
}

.form-input-container{
    flex-wrap: wrap;
}

.time-period{
    @include btn(rgb(170, 170, 170));
}

.time-period-select{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 5px;
}

.selected{
    background-color: rgb(161, 238, 255);
}

#warn-p{
    text-align: left;
    font-size: 12px;
    color:  rgb(250, 85, 129) !important;
}


</style>